class App extends Component {
    constructor(props) {
      super(props);
      let conf = getSetHost(config, false);
      this.state = {
          ...conf
      }
     
  }

  onSortEnd = ({oldIndex, newIndex}) => {
      const {sections} = this.state;

      this.setState({
          sections: arrayMove(sections, oldIndex, newIndex),
      });

      getSetHost( { ...this.state } )
  };

  sectionPreviewChange = (index, status) => {
      let state = {...this.state}
      state.sections[index].show_preview = !status;
      state.sections[index].show_edit_preview = !status;
      state.sections[index].inserted_edit_id = false;
      this.setState({ state });
      getSetHost( { ...state } )
  }

  sectionInputChange = (id, index, value, fieldIndex) => {
      let state = { ...this.state }
      let sectionIndex = findIndex(state.sections, { id });

      if (fieldIndex === null) {
          state.sections[sectionIndex].inputs[index].value = value;
      } else {
          state.sections[sectionIndex].inputs[index][fieldIndex].value = value;
      }
      this.setState({ state });
      getSetHost( { ...state } )
  }

  addFieldInsert = async (id, fields, static_input) => {
      let state = { ...this.state }
      let index = findIndex(state.sections, { id });
      let pushItem = cloneDeep(fields);
      state.sections[index].inserted.push(pushItem);
      this.setState({ state });
      getSetHost( { ...state } )
  }

  updatedFieldInsert = async (id, fields, static_input, inserted_edit_id) => {
      let state = { ...this.state }
      let index = findIndex(state.sections, { id });
      let pushItem = cloneDeep(fields);
      state.sections[index].inserted[inserted_edit_id] = pushItem;
      this.setState({ state });
      getSetHost( { ...state } )
  }

  editDynamicInsertedItem = async (id, index, show_preview) => {
      let state = { ...this.state }
      let sectionIndex = findIndex(state.sections, { id });
      state.sections[sectionIndex].inputs = cloneDeep(state.sections[sectionIndex].inserted[index]);
      state.sections[sectionIndex].show_preview = !show_preview;
      state.sections[sectionIndex].show_edit_preview = true;
      state.sections[sectionIndex].inserted_edit_id = index;
      this.setState({ state });
      getSetHost( { ...state } )
  }

  removeInsertedItem = async (id, index) => {
      let state = { ...this.state }
      let sectionIndex = findIndex(state.sections, { id });
      state.sections[sectionIndex].inserted = cloneDeep(state.sections[sectionIndex].inserted.filter((item, key) => {
          return key !== index;
      }));
      this.setState({ state });
      getSetHost( { ...state } )
  }

  cancelInputFix = async (id, static_input) => {
      let state = { ...this.state }
      let index = findIndex(state.sections, { id })

      if (! static_input) {
          state.sections[index].inputs[0] = state.sections[index].inputs[0].map((item) => {
              item.value = '';
              return item
          })
      } 

      this.setState({ state })
      getSetHost( { ...state } )
  }

  changeTemplate = (id) => {
      const state = { ...this.state }
      state.template = id;
      this.setState({ template: id });
      getSetHost( { ...state } )
  }

  render() {
    const {template, sections} = this.state;
    return (
      <div className="App">
        <div className="tab-wrap">

          <input type="radio" id="tab1" name="tabGroup1" className="tab" />
          <label htmlFor="tab1"><span className="fas fa-file-invoice"></span> Templates</label>

          <input type="radio" id="tab2" name="tabGroup1" className="tab" defaultChecked={true} />
          <label htmlFor="tab2"><span className="fas fa-pencil-alt"></span> Editor</label>

          <input type="radio" id="tab3" name="tabGroup1" className="tab"/>
          <label htmlFor="tab3"><span className="fas fa-search"></span> Preview</label>

          <div className="tab__content">
            <Template 
                template={template}
                changeTemplate={this.changeTemplate}
            />
          </div>

          <div className="tab__content">
            <Resume 
              items={sections} 
              onSortEnd={this.onSortEnd} 
              useDragHandle={true}
              changeSectionPreview={this.sectionPreviewChange}
              changeInputValue={this.sectionInputChange}
              addFieldInsert={this.addFieldInsert}
              cancelInputFix={this.cancelInputFix}
              editDynamicInsertedItem={this.editDynamicInsertedItem}
              updatedFieldInsert={this.updatedFieldInsert}
              removeInsertedItem={this.removeInsertedItem}
            />
          </div>

          <div className="tab__content">
            <Preview 
                template={template}
                sections={sections}
            />
          </div>

        </div>
      </div>
    );
  }
}

export default App;



