import React, { Component } from 'react';
import { findIndex } from 'lodash';

import TraditionalPreview from './_templates/traditional';

class Preview extends Component {
    constructor(props) {
        super(props);
    }

    componentWillReceiveProps(props) {
        this.props = props;
    }

    splitSections = ( id ) => {
        const sectionIndex = findIndex(this.props.sections, { id });
        return this.props.sections[sectionIndex];
    }

    showPreview = () => {
        const { template } = this.props;
        if ( template === 1) {
            return (
                <TraditionalPreview
                  sections={this.props.sections}
                  contact_details={this.splitSections(0)}
                  education={this.splitSections(1)}
                  employment_history={this.splitSections(2)}
                  hobbies={this.splitSections(3)}
                  skills={this.splitSections(4)}
                  language={this.splitSections(5)}
                  references={this.splitSections(6)}
                  summary={this.splitSections(7)}
                />
            )
        } 

    }


    render() {
        return (
            <div className="container">
                <div className="resume_wrapper">
                    <div className="resume">
                        {
                            this.showPreview()
                        }
                    </div>
                </div>  
            </div>
        );
    }
}

export default Preview;


