import React, { Component } from 'react';
import {
    SortableHandle,
    SortableElement
} from 'react-sortable-hoc';
import RichTextEditor from 'react-rte';

const DragHandle = SortableHandle(() => <span className="fas fa-arrows-alt" style={{ cursor: 'pointer' }}></span>); // This can be any component you want


class SortableItem extends Component {

    constructor(props) {
        super(props);
        this.state = {
            value: RichTextEditor.createEmptyValue()
        }
    }

    onChange = (value) => {
        this.setState({value});
        if (this.props.onChange) {
          // Send the changes up to the parent component as an HTML string.
          // This is here to demonstrate using `.toString()` but in a real app it
          // would be better to avoid generating a string on each change.
          this.props.onChange(
            value.toString('html')
          );
        }
    }

    componentWillReceiveProps(props) {
        this.props = props;
    }

    changeAddress = (address = '', city = '', state_province = '', zipCode = '', country = '') => {
        let addrArr = [];
        city ? addrArr.push(city) : null ;
        state_province ? addrArr.push(state_province): null;
        country ? addrArr.push(country) : null;

        let addrString = addrArr.join(',');
        return (
            <p>{address} <br/> { addrString } { zipCode }</p>
        );
    }

    previewData = (input = null, index = null) => {
        const { value: { id, inputs, preview_alignment} } = this.props;
        if (id === 0) {
            let firstName = inputs[0].value || '';
            let lastName = inputs[1].value || '';

            let address = this.changeAddress(
                inputs[4].value,
                inputs[5].value,
                inputs[6].value,
                inputs[7].value,
                inputs[8].value   
            );

            let phoneNumber = inputs[2].value || '';
            let email = inputs[3].value || '';
            return(
                <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                    <h3>{firstName } {lastName}</h3>
                    { address }
                    <p>{ phoneNumber }</p>
                    <p> { email } </p>   
                </div>
            );
        } else if (id === 1) {
            if (input) {
                let major = input[0].value || '';
                let school = input[1].value || '';
                let status = input[2].value || '';
                let location = input[3].value || '';
                let month = input[4].value || '';
                let year = input[5].value || '';
                let graduation = () => {
                    return ((status && month && year) ? `${status}: ${month} ${year}`: '');
                }
                let description = input[6].value || '';
                return(
                    <div>
                        <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                            <h4>{major}</h4>
                            <p style={{ margin: 0 }}>{school}</p>
                            <p style={{ margin: 0 }}>{location}</p>
                            <p style={{ margin: 0 }}>{graduation()}</p>
                            <p>{description}</p>
                        </div>
                        
                    </div>
                );    
            }
            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>

                    </div>
                    
                </div>
            );

        } else if (id === 2) {
            
            if (input) {
                let jobTitle = input[0].value || '';
                let companyName = input[1].value || '';
                let location = input[2].value || '';
                let formMonth = input[3].value || '';
                let formYear = input[4].value || '';
                let toMonth = input[5].value || '';
                let toYear = input[6].value || '';
                let currentlyWorkHere = input[7].value;
                let setTimeDate = () => {
                    if (currentlyWorkHere) {
                        return `${formMonth} ${formYear} - present`;
                    } else {
                        return `${formMonth} ${formYear} - ${toMonth} ${toYear}`;
                    }
                }
                return(
                    <div>
                        <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                            <h3>{jobTitle}</h3>
                            <p>{companyName}</p>
                            <p>{location}</p>
                            <p>{setTimeDate()}</p>
                        </div>
                    </div>
                );
            }

            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                    </div>
                </div>
            );

        } else if (id === 3) {
            let hobbies = inputs[0].value || '';
            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                        <p>{ hobbies }</p>
                    </div>
                </div>
            );
        } else if (id === 4) {
            
            if (input) {
                let skill = input[0].value || '';
                let status = input[1].value || '';

                return(
                    <div>
                        <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                            <h2>{`${skill} : ${status}`}</h2>  
                        </div>
                        
                    </div>
                );
            }

            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                        {/* <h2>{`${skill} `}</h2>   */}

                    </div>
                    
                </div>
            );
        } else if (id === 5) {
            
            if (input) {
                let lang = input[0].value || '';
                let status = input[1].value || '';

                return(
                    <div>
                        <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                            <h2>{`${lang} : ${status}`}</h2>  
                        </div>
                        
                    </div>
                );
            }

            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                        {/* <h2>{`${skill} `}</h2>   */}

                    </div>
                    
                </div>
            );
        } else if (id === 6) {
            if (input) {
                let name = input[0].value || '';
                let relation = input[1].value || '';
                let company = input[2].value || '';
                let email = input[3].value || '';
                let address = input[4].value || ''

                return(
                    <div>
                        <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                            <h4>{name}</h4>
                            <p>{ relation }</p>
                            <p>{ company }</p>
                            <p>{ email }</p>
                            <p>{ address }</p>
                        </div>
                        
                    </div>
                );
            }

            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                        {/* <h2>{`${skill} `}</h2>   */}

                    </div>
                    
                </div>
            );
        } else if (id === 7) {
            let summary = inputs[0].value || '';
            return(
                <div>
                    <div className="section_dotted" style={{ textAlign: preview_alignment, padding: '10px 15px' }}>
                    <p>{ summary }</p>
                    </div>
                    
                </div>
            );
        }
    }

    setPreviewOnScreen = () => {
        let { value: { id, show_preview, static_input, inserted } } = this.props;
        
        if (! static_input ) {
            if (inserted.length) {
                return inserted.map((item, index) => {
                    return (
                        <div key={index}>
                            <div className="section_preview">
                                    <div className="section_hover" style={{ padding: '40px' }}>
                                        <span 
                                            className="fas fa-pencil-alt fa-3x edit-icon" 
                                            style={{ padding: '25px' }}
                                            onClick={(e) => this.props.editDynamicInsertedItem(id, index, show_preview)}
                                        ></span>
                                        <span 
                                            className="fas fa-trash-alt fa-3x edit-icon" 
                                            style={{ padding: '20px' }}
                                            onClick={(e) => this.props.removeInsertedItem(id, index)}
                                        ></span>
                                    </div>
                                    <div >
                                        { this.previewData(item[0], index) }
                                    </div>
                                    
                            </div>
                            
                        </div>
                    );
                })
            } else {
                return (
                    <div>
                        <div className="section_preview">
                                <div className="section_hover" onClick={() => this.props.changeSectionPreview(id, show_preview)}>
                                    <span className="far fa-edit fa-3x"></span>
                                </div>
                                <div >
                                    { this.previewData() }
                                </div>
                                
                        </div>
                        
                    </div>
                );
            }

        } else {
            return (
                <div>
                    <div className="section_preview">
                            <div className="section_hover" onClick={() => this.props.changeSectionPreview(id, show_preview)}>
                                <span className="far fa-edit fa-3x"></span>
                            </div>
                            <div >
                                { this.previewData() }
                            </div>
                            
                    </div>
                    
                </div>
            );
        }
    }

    addNewItemSectionPreview = (id, show_preview, static_input) => {
        this.props.changeSectionPreview(id, show_preview)
        this.props.cancelInputFix(id, static_input);
    }

    addNewItem = (id, static_input) => {
        if (! static_input ) {
            let { value: { id, inserted, show_preview } } = this.props;
            if ((id === 1 || id === 2 || id === 4 || id === 5 || id === 6) && inserted.length) {
                return (
                    <button 
                        className="btn btn-primary btn-block"
                        onClick={() => this.addNewItemSectionPreview(id, show_preview, static_input)}
                    >
                        Add New Item
                    </button>
        
                );
            }
        }
    }

    addValue = (id, fields ,static_input, show_preview) => {
        if (! static_input) {
            this.props.addFieldInsert(id, fields, static_input);
            this.props.changeSectionPreview(id, show_preview)
        } else {
            this.props.changeSectionPreview(id, show_preview)
        }
    }

    updatedValue = (id, fields ,static_input, show_preview, inserted_edit_id) => {
        if (! static_input) {
            this.props.updatedFieldInsert(id, fields, static_input, inserted_edit_id);
            this.props.changeSectionPreview(id, show_preview)
        } else {
            this.props.changeSectionPreview(id, show_preview)
        }
    }

    cancelButton = (id, show_preview, static_input) => {
        this.props.cancelInputFix(id, static_input)
        this.props.changeSectionPreview(id, show_preview)
    }

    renderAddOrEditButton = (id, inputs ,static_input, show_preview, show_edit_preview, inserted_edit_id) => {
        if (! show_edit_preview) {
            return (
                <div className="row">
                    <div className="col-xs-12 col-sm-6">
                        <button className="btn btn-warning btn-block" onClick={() => this.cancelButton(id, show_preview, static_input)}>Cancel</button>
                    </div>
                    <div className="col-xs-12 col-sm-6">
                        <button className="btn btn-success btn-block" onClick={() => this.addValue(id, inputs ,static_input, show_preview)}>Add</button>
                    </div>
                </div>
            )
        } else {
            return (
                <div className="row">
                    <div className="col-xs-12 col-sm-6">
                        <button className="btn btn-warning btn-block" onClick={() => this.cancelButton(id, show_preview, static_input)}>Cancel</button>
                    </div>
                    <div className="col-xs-12 col-sm-6">
                        <button className="btn btn-success btn-block" onClick={() => this.updatedValue(id, inputs ,static_input, show_preview, inserted_edit_id)}>Update</button>
                    </div>
                </div>
            )
        }
    }

    checkSectionPreview = () => {
        let { value: { id, show_preview,inserted, inserted_edit_id, fields, section_name, inputs, show_edit_preview, static_input } } = this.props;

        if (show_preview) {
            return (
                <div className="section_bottom_padding">
                    {
                        this.setPreviewOnScreen()
                    }
                    {
                        this.addNewItem(id, static_input)
                    }
                </div>
            );
        } else {
            return (
                <div className="section_input">
                    {
                        inputs.map((item, index) => {
                            let input = item;
                            if (static_input) {
                                if (item.type === 'input') {
                                    return (
                                        <div className={`form-item ${item.grid}`} key={index}>
                                            <input 
                                                type={item.form_type} 
                                                className="form-control" 
                                                id="email" 
                                                placeholder={item.label}
                                                onChange={(e) => this.props.changeInputValue(id, index, e.target.value, null)}
                                                value={item.value} 
                                            />
                                        </div>
                                    )
                                } else if (item.type == 'select' ) {
                                    return (
                                        <div className={`form-item ${item.grid}`} key={index}>
                                            <select 
                                                className="form-control" 
                                                value={item.value} 
                                                onChange={(e) => this.props.changeInputValue(id, index, e.target.value, null)}>
                                                {
                                                    item.options.map((item, index) => {
                                                        return (
                                                            <option key={index}> { item } </option>
                                                        )
                                                    })
                                                }
                                            </select>
                                        </div>
                                    )
                                } else {
                                    return (
                                        <div className={`form-item ${item.grid}`} key={index}>
                                            <textarea 
                                                className="form-control" 
                                                placeholder={item.label} 
                                                rows="5" 
                                                value={item.value} 
                                                onChange={(e) => this.props.changeInputValue(id, index, e.target.value, null)}></textarea>
                                        </div>
                                    )
                                }
                            } else {
                                return input.map((fields, indexField) => {
                                    if (fields.type === 'input') {
                                        return (
                                            <div className={`form-item ${fields.grid}`} key={indexField}>
                                                <input 
                                                    type={fields.form_type} 
                                                    className="form-control" 
                                                    id="email" 
                                                    placeholder={fields.label}
                                                    onChange={(e) => this.props.changeInputValue(id, index, e.target.value, indexField)}
                                                    value={fields.value} 
                                                />
                                            </div>
                                        )
                                    } else if (fields.type === 'select' ) {
                                        if (id === 2 && (indexField === 5 || indexField === 6) && input[7].value) {
                                            return (
                                                <div className={`form-item ${fields.grid}`} key={indexField}>
                                                    <select
                                                        disabled={true}
                                                        className="form-control" 
                                                        value={fields.value} 
                                                        onChange={(e) => this.props.changeInputValue(id, index, e.target.value, indexField)}>
                                                        {
                                                            fields.options.map((item, index) => {
                                                                return (
                                                                    <option key={index}> { item } </option>
                                                                )
                                                            })
                                                        }
                                                    </select>
                                                </div>
                                            )    
                                        }
                                        return (
                                            <div className={`form-item ${fields.grid}`} key={indexField}>
                                                <select 
                                                    className="form-control" 
                                                    value={fields.value} 
                                                    onChange={(e) => this.props.changeInputValue(id, index, e.target.value, indexField)}>
                                                    {
                                                        fields.options.map((item, index) => {
                                                            return (
                                                                <option key={index}> { item } </option>
                                                            )
                                                        })
                                                    }
                                                </select>
                                            </div>
                                        )
                                    } else if (fields.type === 'checkbox') {
                                        return (
                                            <div className={`form-item ${fields.grid}`} key={indexField}>
                                                  <label>
                                                    <input 
                                                        type="checkbox" 
                                                        defaultChecked={fields.value} 
                                                        value={fields.value}
                                                        onChange={(e) => {
                                                            let val = e.target.value === "true" ? false : true;
                                                            this.props.changeInputValue(id, index, val, indexField)
                                                            console.log(typeof val)
                                                        }}
                                                    /> {fields.label}</label>
                                            </div>
                                        )
                                    } else {
                                        return (
                                            <div className={`form-item ${fields.grid}`} key={indexField}>
                                                <textarea 
                                                    className="form-control" 
                                                    placeholder={fields.label} 
                                                    rows="5" 
                                                    value={fields.value} 
                                                    onChange={(e) => this.props.changeInputValue(id, index, e.target.value, indexField)}></textarea>
                                            </div>
                                        )
                                    }
                                })
                            }
                            
                        })
                    }

                    {
                        this.renderAddOrEditButton(id, inputs ,static_input, show_preview, show_edit_preview, inserted_edit_id)
                    }
                </div>
            );
        }
    }

    render() {
        let { value: { id, show_preview, section_name } } = this.props;
        return(
            <li>
                <section>
                    <span className="section_title"><DragHandle />  {section_name}  <i className="fas fa-pencil-alt"
                        onClick={() => console.log('ess')}
                        style={{ cursor: 'pointer' }}
                    ></i>
                    </span>
                    { this.checkSectionPreview() }
                    
                </section>
            </li>
        )
    }
}


export default SortableElement(SortableItem);


