import React, { Component } from 'react';
import { SortableContainer } from 'react-sortable-hoc';
import SortableItem from './sortableitem';

class SortableList extends Component {
    constructor(props) {
        super(props);
    }

    componentWillReceiveProps(props) {
        this.props = props;
    }

    render() {
        return(
            <ul>
            {this.props.items.map((value, index) => (
                <SortableItem 
                    key={`item-${index}`} 
                    index={index} 
                    value={value}
                    changeSectionPreview={this.props.changeSectionPreview} 
                    changeInputValue={this.props.changeInputValue}
                    addFieldInsert={this.props.addFieldInsert}
                    cancelInputFix={this.props.cancelInputFix}
                    editDynamicInsertedItem={this.props.editDynamicInsertedItem}
                    updatedFieldInsert={this.props.updatedFieldInsert}
                    removeInsertedItem={this.props.removeInsertedItem}
                />
            ))}
            </ul>
        );
    }
}

export default SortableContainer(SortableList);


