import React, { Component } from 'react';
import TemplatePic from '../images/1.png';

class Template extends Component {
    constructor(props) {
        super(props);
        
    }

    componentWillReceiveProps(props) {
        this.props = props;
    }

    changeTemplateByComp = (id) => {
        this.props.changeTemplate(id);
    }

    changeHoverActive = (id) => {
        return this.props.template === id ? 'item_hover_active' : 'item_hover';
    }

    render() {
        return (
            <div className="container">
                <div className="template_wrapper">
                    <div className="row">
                        <div className="col-xs-12 col-sm-3 template_item">
                            <div className={this.changeHoverActive(1)} onClick={() => this.changeTemplateByComp(1)}>
                                <span className="far fa-check-square fa-3x" style={{ paddingTop: '145px' }}></span>
                            </div>
                            <div className="item_wrapper">
                                <div className="item-img">
                                    <img src={TemplatePic} className="img-responsive img-reform"/>
                                </div>
                                <div className="item-title">
                                    <p>Hello World</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-3 template_item">
                            <div className="item_wrapper">
                                <div className={this.changeHoverActive(2)} onClick={() => this.changeTemplateByComp(2)}>
                                    <span className="far fa-check-square fa-3x" style={{ paddingTop: '145px' }}></span>
                                </div>
                                <div className="item-img">
                                    <img src={TemplatePic} className="img-responsive img-reform"/>
                                </div>
                                <div className="item-title">
                                    <p>Hello World</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-3 template_item">
                            <div className="item_wrapper">
                                <div className={this.changeHoverActive(3)} onClick={() => this.changeTemplateByComp(3)}>
                                    <span className="far fa-check-square fa-3x" style={{ paddingTop: '145px' }}></span>
                                </div>
                                <div className="item-img">
                                    <img src={TemplatePic} className="img-responsive img-reform"/>
                                </div>
                                <div className="item-title">
                                    <p>Hello World</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-3 template_item">
                            <div className="item_wrapper">
                                <div className={this.changeHoverActive(4)} onClick={() => this.changeTemplateByComp(4)}>
                                    <span className="far fa-check-square fa-3x" style={{ paddingTop: '145px' }}></span>
                                </div>
                                <div className="item-img">
                                    <img src={TemplatePic} className="img-responsive img-reform"/>
                                </div>
                                <div className="item-title">
                                    <p>Hello World</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Template;


