export default function getSethost(state, update = true) {
    const storage = window.localStorage;
    const hostKey = '_cv_builder_react';
    const getHost = JSON.parse(storage.getItem(hostKey))

    if ( update ) {
        storage.removeItem(hostKey);
        storage.setItem(hostKey, JSON.stringify(state));
        return JSON.parse(storage.getItem(hostKey));
    }

    if ( getHost ) {
        return getHost;
    } else {
        storage.setItem(hostKey, JSON.stringify(state));
        return JSON.parse(storage.getItem(hostKey));
    }


}


